
/* eslint-disable indent,camelcase,prefer-promise-reject-errors */

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'

import UtilMixin from '@/mixins/UtilMixin.vue'
import FormMixin from '@/mixins/FormMixin.vue'

export default {
mixins: [UtilMixin, FormMixin],
data() {
return {
groupColor: '#50b97b',
reqId: null,
partnerId: 'yousu',
name: null,
mobile: null,
certNo: null,
userName: null,
pwd: null,
smsCode: null
}
},
components: {
XInput,
Group,
XButton,
Box
},
computed: {},
methods: {
formMixin_submit(ajaxUrl) {

const ISEMPTY = 0
const ISNOVALID = 1
return new Promise((resolve, reject) => {
let params = {}
for (let key in this.$refs) {
if (this.$refs[key].required && this.$refs[key].currentValue === '') {
this.$vux.toast.text('请将信息填写完整', 'top')
this.$refs[key].focus()
reject({
code: ISEMPTY,
msg: '信息填写不完整'
})
return
} else {
if (!this.$refs[key].valid) {
this.$vux.toast.text(this.$refs[key].firstError, 'top')
this.$refs[key].focus()
reject({
code: ISNOVALID,
msg: '信息填写不完整'
})
return
}
}
if (this.$refs[key].name) {
params[this.$refs[key].name] = this.$refs[key].currentValue
}
}

this.$vux.loading.show({
text: '等待中...'
})
this.submitLoadding = true
if (this.type !== null) {
params = {
url: 'https://credit.baiqishi.com/clweb/api/jd/login',
data: params
}
}
this.$axios._post({
url: ajaxUrl,
data: params,
tips: true
}).then(result => {
this.$vux.loading.hide()
this.submitLoadding = false
this.$vux.toast.show({
text: '认证成功'
})
resolve(result)
}).catch(err => {
this.submitLoadding = false
this.$vux.loading.hide()
if (err.code === 'CCOM3069') {
this.reqId = err.data.reqId
this.$vux.toast.show({
text: '验证码已发送'
})
resolve(err.data.reqId)
} else {
this.$vux.toast.show({
type: 'cancel',
text: err.msg
})
reject({
code: err.code,
msg: err.msg
})
}
})
})
},
submit(type) {
if (type === 0) {
this.smsCode = ''
}
if (type === 1 && !this.smsCode) {
this.$vux.toast.show({
type: 'cancel',
text: '请填写验证码'
})
return
}
this.formMixin_submit('/bqs_api/')
.then((res) => {
console.log(res)
if (type === 1) {
this.$router.push({name: 'SelectAuth', query: {checkway: this.$route.query.checkway}})
}
})
.catch((err) => {
console.log(`code:${err.code} \n msg:${err.msg}`)
})
}
},
created() {

},
mounted() {
}
}



